'use client'
import React from 'react'
import { motion } from "motion/react"
import Image from 'next/image'
import { Minus, Plus, ShoppingCart, Star } from 'lucide-react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, RootState } from '@/redux/store'
import { addToCart, decreaseQuantity, increaseQuantity } from '@/redux/cartSlice'

interface IProduct {
  _id: string,
  name: string,
  category: string,
  price: string,
  unit: string,
  image: string,
  createdAt?: Date,
  updatedAt?: Date
}

function ProductItemCard({ item }: { item: IProduct }) {
  const dispatch = useDispatch<AppDispatch>()
  const { cartData } = useSelector((state: RootState) => state.cart)
  const cartItem = cartData.find(i => i._id.toString() == item._id)

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.95 }}
      whileInView={{ opacity: 1, scale: 1 }}
      whileHover={{ y: -10 }}
      transition={{ duration: 0.4, ease: "easeOut" }}
      viewport={{ once: true }}
      className='bg-white rounded-[2rem] p-4 shadow-2xl shadow-green-900/5 group relative flex flex-col h-full border border-zinc-100 hover:border-green-200 transition-all'
    >
      <div className='relative w-full aspect-square bg-zinc-50 rounded-[1.5rem] overflow-hidden mb-5 group-hover:shadow-inner'>
        <Image
          src={item.image}
          fill
          alt={item.name}
          sizes='(max-width: 768px) 100vw, 25vw'
          className='object-contain p-6 transition-transform duration-700 group-hover:scale-110'
        />
        <div className='absolute top-3 right-3 bg-white/80 backdrop-blur-md px-2 py-1 rounded-xl flex items-center gap-1 shadow-sm'>
          <Star size={12} className='text-yellow-500 fill-yellow-500' />
          <span className='text-[10px] font-bold text-zinc-700'>4.9</span>
        </div>
      </div>

      <div className='flex flex-col flex-1 px-1'>
        <div className='flex items-center justify-between mb-2'>
          <span className='text-[10px] font-black uppercase tracking-widest text-green-600 bg-green-50 px-2 py-1 rounded-lg'>
            {item.category}
          </span>
          <span className='text-[10px] font-bold text-zinc-400'>
            {item.unit}
          </span>
        </div>

        <h3 className='text-zinc-900 font-bold text-lg leading-tight mb-4 group-hover:text-green-600 transition-colors'>
          {item.name}
        </h3>

        <div className='mt-auto flex items-center justify-between gap-4'>
          <div className='flex flex-col'>
            <span className='text-[10px] text-zinc-400 font-bold uppercase'>Price</span>
            <span className='text-zinc-900 font-black text-2xl'>‚Çπ{item.price}</span>
          </div>

          {!cartItem ? (
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              onClick={() => dispatch(addToCart({ ...item, quantity: 1 }))}
              className='w-12 h-12 bg-zinc-900 text-white rounded-2xl flex items-center justify-center shadow-lg shadow-zinc-900/20 group-hover:bg-green-600 group-hover:shadow-green-500/30 transition-all'
            >
              <ShoppingCart size={20} />
            </motion.button>
          ) : (
            <div className='flex items-center bg-zinc-100 rounded-2xl p-1 gap-3 border border-zinc-200'>
              <button
                className='w-8 h-8 flex items-center justify-center rounded-xl bg-white hover:bg-red-50 hover:text-red-500 transition-colors shadow-sm'
                onClick={() => dispatch(decreaseQuantity(item._id))}
              >
                <Minus size={14} />
              </button>
              <span className='text-sm font-black text-zinc-900 min-w-[20px] text-center'>{cartItem.quantity}</span>
              <button
                className='w-8 h-8 flex items-center justify-center rounded-xl bg-white hover:bg-green-50 hover:text-green-500 transition-colors shadow-sm'
                onClick={() => dispatch(increaseQuantity(item._id))}
              >
                <Plus size={14} />
              </button>
            </div>
          )}
        </div>
      </div>
    </motion.div>
  )
}

export default ProductItemCard


